























































import { Vue, Component } from 'vue-property-decorator'
import MultipleSearchInput, { SelectOption } from './lib/multiple-search-input.vue'
import {enableLogs} from './lib/index'
enableLogs(true)
@Component({
  components: {
    MultipleSearchInput
  }
})
export default class App extends Vue {
  firstVal = []
  curOptiton: SelectOption<string>[] = []
  isLoading = false
  list = [
    {
      text:'Apple' , value: 'a'
    },
    {
      text:'Bear' , value: 'b'
    },
    {
      text:'Cat' , value: 'c'
    }
  ]

  alreadyVal = ['a', 'b']
  optitonList = [
    {
      text:'Apple' , value: 'a'
    },
    {
      text:'Bear' , value: 'b'
    },
  ]
  longList: SelectOption<string>[] = []
  withTrimVal = [' a ']
  withTrimList = [' a ']
  asyncList: SelectOption<string>[] = []
  asyncDefaultValue: SelectOption<string>[] = []
  thirdVal: string[] = []
  forthVal: string[] = []
  fifthVal: string[] = ['a']
  longVal: string[] = []
  page = 1
  total = 200
  loadedAll = false
  busy = false

  created() {
  setTimeout(() => {
     this.forthVal = ['a']
    }, 1000)
    setTimeout(() => {
      this.asyncDefaultValue = [...this.list]
    }, 1000)
    for(let i = 0; i< 50;i++) {
      this.longList.push({
        text: "" + i,
        value: "" + i
      })
    }
  }

  getCurOptiton(optiton: SelectOption<string>[]){
    this.curOptiton = optiton
  }

  fetchData(val: string) {
    console.log('fetch', val)
    this.isLoading = true
    setTimeout(() => {
      console.log(val)
      this.asyncList = this.list.filter(el => el.text.includes(val))
       this.isLoading = false
    }, 5000)
  }

  fetchOtherData(val: string) {
    this.busy = true
     setTimeout(() => {
      this.longList = this.getFakeData(val)
      this.page = 1
      this.total = 100
      this.loadedAll = false
    }, 1000)
  }

  getFakeData(val: string) {
    let list = []
    for(let i = 0; i<50;i++) {
      list.push({
        text: i + val,
        value: i + val
      })
    }
    return list
  }

  fetchMoreData(val: string) {
    console.log('load')
    if(!this.loadedAll) {
      this.busy = true
      setTimeout(() => {
        const page = this.page
         for(let i = page * 50; i< page * 50 + 50; i++) {
          this.longList.push({
          text: val + i,
          value: val + i
          })
        }
        this.busy = false
        this.page ++
        this.loadedAll = this.page * 50 === this.total
      }, 1000)
    }
  }
  handleAdd(){
    this.forthVal.push('c')
  }
}
